a {
  color: inherit;
}
* {
  border-color: var(--bc1);
  -webkit-text-stroke-color: var(--sc1);
}
*::selection {
  background: var(--selection-bg);
  color: var(--selection-fc);
}
img::selection {
  background: transparent;
}
